/* Setting drawer CSS */
.setting_drawer {
  width: 500px;
  height: 100vh;
  flex-shrink: 0;
  border-radius: 20px 0px 0px 20px;
  background: var(--white, #fff);
  box-shadow: 20px 9px 71.3px 0px rgba(149, 161, 184, 0.26);
  transition: transform 0.3s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
  transform: translateX(0);
  /* overflow-y: auto; */
}

.setting_icons {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 39px;
  margin: 20px 30px;
}

