.setting_icons_top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 117px;
  position: fixed; /* Fixes the element at the top */
  top: 0;
  left: 0;
  width: 100%; /* Covers the full width of the screen */
  z-index: 1000; /* Brings it to the front */
  background-color: white; /* Background color to avoid transparency */
  padding: 10px 20px; /* Adds padding for spacing */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Optional shadow */
  border-bottom: 1px solid #ededf3; /* Optional bottom border */
}


