.setting_filter {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 28px;
  padding: 60px 30px;
  overflow-y: auto; /* Allows vertical scrolling */
  overflow-x: hidden; /* Prevent horizontal scrolling */
  /* width: 100%; Ensure it doesn't overflow the parent container */
  box-sizing: border-box; /* Include padding and border in width calculation */
  max-height: calc(100vh - 60px); /* Set a maximum height to prevent overflow */
}


.setting_filter_top {
  display: flex;
  align-items: flex-start;
  gap: 145px;
}

